import Order_items from '../components/order_item/Order_item'
import axios from 'axios'
axios.defaults.withCredentials=true;
import Link from 'next/link'
const Order=()=>{
    let data=[]
    axios.get('http://localhost:3002/cookie').then(response=>{
        axios.get(`http://localhost:3002/cart/get_placed_order/${response.data}`)
        .then(response=>{
            data=response.data
        })
    })
   console.log(data)
    return(
        <div id='main'><Link href='/details'>yes</Link>
        <style jsx>
            {`
            #main:hover{
                transform:scale(1.1);
            }
            #main:active{
                transform:scale(0.9);
            }
            `}
        </style>
        </div>
    )
}

export default Order;


